body {
	min-width: 320px;
}

div.container-top-margin {
	margin-top: 5%;
}

ul#postDetailsList {
	list-style-type: none;
	padding: 0;
	margin: 1.5%;
}

span.postDetailsHeading {
	display: inline-block;
	width: 125px;
}

form#deletePostForm, form#closePostForm {
	display: inline;
}

form#filterResultsForm {
	margin-bottom: 5%;
}

p#postingsNote, a#openPostingsLink, a#archivedPostingsLink {
	font-weight: bold;
}